\n            color: $color_white;\r\n        }\r\n\r\n        h3 {\r\n            margin: 0 0 32px 0;\r\n            text-align: center;\r\n        }\r\n    }\r\n}\r\n\r\n// Wrapper for hero element\r\n.heroWrapper {\r\n    width: 100%;\r\n\r\n    // Inner content container for the hero\r\n    .heroContainer {\r\n        display: grid;\r\n        grid-template-columns: minmax(0, 1fr) 1fr;\r\n        grid-template-rows: repeat(2, auto-fit);\r\n        box-sizing: border-box;\r\n\r\n        max-width: 1280px; // todo: consider increasing on 4K displays\r\n        margin: 0 auto; // center the hero container\r\n        padding: 0 16px 64px 16px; // 64px to cater for next hero image overlap\r\n\r\n        &.noBottomPadding {\r\n            padding: 0 16px;\r\n        }\r\n    }\r\n\r\n    // -- Image alignments\r\n    &.heroImageLeft {\r\n        .heroImageWrapper {\r\n            grid-area: 1 / 1 / 2 / 2;\r\n        }\r\n\r\n        .sectionHeader {\r\n            grid-area: 1 / 2 / 2 / 3;\r\n        }\r\n\r\n        .contentWrapper {\r\n            grid-area: 2 / 1 / 3 / 3;\r\n        }\r\n    }\r\n\r\n    &.heroImageRight {\r\n        .heroImageWrapper {\r\n            grid-area: 1 / 2 / 2 / 3;\r\n        }\r\n\r\n        .sectionHeader {\r\n            grid-area: 1 / 1 / 2 / 2;\r\n        }\r\n\r\n        .contentWrapper {\r\n            grid-area: 2 / 1 / 3 / 3;\r\n        }\r\n    }\r\n\r\n    &.heroNoImage {\r\n        .sectionHeader {\r\n            grid-area: 1 / 1 / 2 / 3;\r\n        }\r\n\r\n        .contentWrapper {\r\n            grid-area: 2 / 1 / 3 / 3;\r\n        }\r\n    }\r\n    // -- End of image alignments\r\n\r\n    // Dark style\r\n    &.heroDark {\r\n        background-color: $color_black;\r\n\r\n        h1,\r\n        h2,\r\n        h3 {\r\n            color: lighten($color_ycblue, 32%);\r\n        }\r\n        p {\r\n            color: $color_white;\r\n        }\r\n    }\r\n\r\n    // Hero image container\r\n    .heroImageWrapper {\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: stretch;\r\n        justify-content: flex-start;\r\n\r\n        max-height: 320px;\r\n        width: 100%;\r\n        margin: -64px 0 0 0; // margin: 0; pins element edges to parent edges\r\n\r\n        box-shadow: 0 0 16px rgba(0, 0, 0, 0.4);\r\n\r\n        transition: $transition_time_movement;\r\n\r\n        .linkButton a {\r\n            width: 100%;\r\n\r\n            box-shadow: none;\r\n\r\n            &:hover {\r\n                box-shadow: none;\r\n            }\r\n        }\r\n\r\n        img {\r\n            object-fit: cover;\r\n            min-height: 240px;\r\n            height: 100%;\r\n            max-width: 100%;\r\n        }\r\n    }\r\n}\r\n\r\n// Outer wrapper for the section\r\n.sectionWrapper {\r\n    width: 100%;\r\n\r\n    // Inner content container\r\n    .sectionContainer {\r\n        display: grid;\r\n        grid-template-columns: 1fr;\r\n        grid-template-rows: repeat(2, max-content);\r\n        box-sizing: border-box;\r\n\r\n        max-width: 1280px; // todo: consider increasing on 4K displays\r\n        margin: 0 auto; // center the hero container\r\n        padding: 0 16px 0 16px;\r\n\r\n        .contentWrapper p {\r\n            margin: 32px 0;\r\n        }\r\n    }\r\n}\r\n\r\n/* Other element styles */\r\nul.listGrid {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, max-content);\r\n    grid-template-rows: repeat(auto-fit 1fr);\r\n\r\n    max-width: max-content; // shouldn't have to do this, but alas, I must.\r\n    margin: 0 auto;\r\n\r\n    li {\r\n        box-sizing: border-box;\r\n\r\n        padding: 8px 24px 8px 8px;\r\n        border-bottom: 2px solid $color_grey;\r\n        font-weight: bold;\r\n\r\n        &:nth-last-child(-n + 2) {\r\n            border-bottom: none; //![last row border] if @media query is updated, this should be double-checked\r\n        }\r\n    }\r\n}\r\n\r\nul.flexRelevantUnits {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: stretch;\r\n    flex-wrap: wrap;\r\n\r\n    .subjectTile {\r\n        position: relative;\r\n\r\n        display: flex;\r\n        flex-direction: column;\r\n        justify-content: center;\r\n        align-items: center;\r\n\r\n        padding: 32px 16px;\r\n        margin: 8px;\r\n        max-width: 320px;\r\n\r\n        text-align: center;\r\n\r\n        color: white;\r\n\r\n        &.fruGreen {\r\n            background-color: $color_green;\r\n        }\r\n\r\n        &.fruLilac {\r\n            background-color: $color_lilac;\r\n        }\r\n\r\n        &.fruOrange {\r\n            background-color: $color_orange;\r\n        }\r\n\r\n        &.fruBlue {\r\n            background-color: $color_blue;\r\n        }\r\n\r\n        &.fruBrown {\r\n            background-color: $color_brown;\r\n        }\r\n\r\n        h4 {\r\n            margin-bottom: 16px;\r\n        }\r\n\r\n        p {\r\n            padding: 0 16px 0 16px;\r\n            margin: 0;\r\n        }\r\n\r\n        .fruContainer {\r\n            .fruInterest {\r\n                position: relative;\r\n\r\n                transition: $transition_time_link;\r\n            }\r\n\r\n            .fruUnits {\r\n                position: absolute;\r\n                top: 0;\r\n                left: 0;\r\n                display: flex;\r\n                flex-direction: column;\r\n                justify-content: center;\r\n                align-items: center;\r\n\r\n                width: 100%;\r\n                height: 100%;\r\n\r\n                opacity: 0;\r\n                transform: scale(0.9);\r\n\r\n                transition: $transition_time_link;\r\n            }\r\n\r\n            &:hover {\r\n                .fruInterest {\r\n                    opacity: 0;\r\n                    transform: scale(1.1);\r\n                }\r\n                .fruUnits {\r\n                    opacity: 1;\r\n                    transform: scale(1);\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.imageCarousel {\r\n    height: auto;\r\n    width: calc(100vw - 32px);\r\n    max-height: 480px;\r\n    max-width: 720px;\r\n\r\n    margin: auto;\r\n    overflow: hidden;\r\n}\r\n\r\n@media only screen and (max-width: $media_screen_max_width) {\r\n    .contentWrapper {\r\n        margin: 0 0 32px 0;\r\n\r\n        &:before {\r\n            display: inline-block;\r\n\r\n            background-color: $color_grey_dark;\r\n\r\n            height: 2px;\r\n            width: 32px;\r\n\r\n            margin: 0 auto;\r\n        }\r\n    }\r\n    .heroWrapper {\r\n        &.heroImageLeft,\r\n        &.heroImageRight {\r\n            .heroImageWrapper {\r\n                grid-area: 1 / 1 / 2 / 2;\r\n            }\r\n\r\n            .sectionHeader {\r\n                grid-area: 2 / 1 / 3 / 2;\r\n            }\r\n\r\n            .contentWrapper {\r\n                grid-area: 3 / 1 / 4 / 2;\r\n            }\r\n        }\r\n\r\n        .heroContainer {\r\n            grid-template-columns: 1fr;\r\n            grid-template-rows: repeat(3, auto-fit);\r\n        }\r\n\r\n        ul.listGrid {\r\n            grid-template-columns: 1fr;\r\n\r\n            li {\r\n                border-bottom: none;\r\n                border-left: 2px solid $color_grey;\r\n            }\r\n        }\r\n    }\r\n}\r\n","$color_foreground: #191919;\r\n$color_background: #fafafa;\r\n\r\n$color_white: #fafafa;\r\n$color_black: #191919;\r\n$color_grey: #dadada;\r\n$color_grey_dark: rgb(116, 117, 136);\r\n$color_ycblue: #21447c;\r\n$color_purple: #736eab;\r\n$color_teal: #27c9a2;\r\n\r\n$color_green: #56d763;\r\n$color_lilac: #a952d1;\r\n$color_orange: #ec712c;\r\n$color_blue: #5f6deb;\r\n$color_brown: #be4e1e;\r\n\r\n$transition_time_link: 0.14s;\r\n$transition_time_movement: 0.19s;\r\n\r\n$fonts_heading: \"Fugaz One\", \"Roboto\", \"Open Sans\", sans-serif;\r\n$fonts_body: \"Nunito\", \"Roboto\", \"Open Sans\", sans-serif;\r\n\r\n$box_shadow_light: 0 4px 8px rgba(0, 0, 0, 0.2);\r\n\r\n$media_screen_max_width: 640px;\r\n"]}]);
